@import url(https://fonts.googleapis.com/css?family=Lato:400,700&display=swap);
.Home {
	background-color: #100f1c;
	background-image: url(/tus-importaciones-chile/static/media/stars.1d17360a.svg);
	height: 85vh;

	color: white;

	display: -webkit-flex;

	display: flex;
	-webkit-justify-content: space-around;
	        justify-content: space-around;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-flex-wrap: wrap;
	        flex-wrap: wrap;
}

.Home__hero-image {
	max-width: 200px;
}


.Home__Title {
	margin-bottom: 5%;
		-webkit-animation: enter 2s linear;
		        animation: enter 2s linear;

}

@-webkit-keyframes enter {

	from {

		opacity: 0;
		-webkit-transform: translateX(-50px);
		        transform: translateX(-50px);
			}

	to {
		opacity: 1;
		-webkit-transform: translateX(0px);
		        transform: translateX(0px);
	}
}

@keyframes enter {

	from {

		opacity: 0;
		-webkit-transform: translateX(-50px);
		        transform: translateX(-50px);
			}

	to {
		opacity: 1;
		-webkit-transform: translateX(0px);
		        transform: translateX(0px);
	}
}

label {
